.bio-page{

    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background: linear-gradient(219deg,#383638,#000000f5,#0a0909d1);
}
.content-bio{
    width: 550px;
    height: 680px;
    background-color: aliceblue;
    /* transform: scale(1);   */
}
/* left-side  */
.left-side{
    width: 200px;
    height: 100%;
    background-color: rgb(31, 35, 35);
    position: relative;
}
.border-pink{
    position: absolute;
    z-index: 10;
    top: 0;
    bottom: 0;
    right: 0.3vw;
    width: 2%;
    background-color: rgb(168, 0, 104);
}
.img-profile{
    height: 18%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    margin: 1.7rem 0;
}
.border-img{
    width: 110px;
    height: 110px;
    border: rgb(168, 0, 104) solid 3px;
    transform: rotate(45deg);
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    left: 25%;
    top:6%
    
}
.son-border-img{
    width: 100%;
    height: 100%;
    border: rgb(47, 56, 56) solid 0.3vw;
    overflow: hidden;
}
.img-profile img{
    width:100%;
    transform: rotate(318deg)scale(1.4,1.4);

}
.addr-sec{
    color: rgb(168, 0, 104) ;
    padding-left: 0.8vw;
}
.circle-bio{
    width: 10px;
    height: 10px;
    background-color:rgb(168, 0, 104) ;
    border-radius: 50%;
    margin-right: 2px;
}
.circle-bio-outline{
    width: 10px;
    height: 10px;
    background-color:  rgb(86, 81, 84) ;
    border-radius: 50%;
    margin-right: 2px;
}
.contact-bio{
    position: absolute;
    bottom: 5px;
}
.contact-bio span{
    padding-right: 4px;
}
/* right side*/
.right-side{
    width:350px;
    height: 100%;

}
.right-side h6{
    color:rgb(168, 0, 104) ;
    font-size: 15px;
}
.section_1{
    padding-left: 20px;
    padding-top: 30px;
    padding-bottom: 20px;
    line-height: 0.1px;
}
.section_1 h4{
    color: rgb(82, 86, 89);
}
.section_1 p{
    font-size: 12px;
    
    color:rgb(59, 59, 59)
}
.section_2{
    padding-left: 20px;
}
.section_2 p{
    font-size: 9px;
    color:rgb(59, 59, 59);
    line-height: 11px;
}

.section_2 span{
    color:rgb(59, 59, 59);
    font-weight: 700;
}
.border-ele{
    border-left:2px solid rgb(168, 0, 104);
    position: relative;
}
.border-ele .cicular{
    height: 10px;
    width: 10px;
    background-color: rgb(168, 0, 104);
    border-radius: 50%;
    position: absolute;
    left: -6px;

}
.border-ele  .content{
    width:300px;
    margin-top: -2px;
    font-size: 8px;
    color:rgb(59, 59, 59);
    line-height: 11px;
}
.date{
    width:150px;
    margin-top: -2px;
    color:rgb(168, 0, 104);
}
.title-content{
    font-size:8px;
    font-weight: 700;
}
.des{
    color: grey;
}
.cont{
    color:rgb(94, 94, 94);
}
.download-icon{
    position: fixed;
    right: 5%;
    bottom: 2%;
    cursor: pointer;

}
.icon{
   
    color:rgb(168, 0, 104);
    animation: changeColor 2s infinite;
}
@keyframes changeColor {
    from{
        color:rgb(168, 0, 104);
    }to{
        color:silver;
    }
    
}
@media (max-width:550px) {
    
    .content-bio{
        transform: scale(0.75);
    }
}
@media (max-width:399px) {
    
    .content-bio{
        transform: scale(0.65);
    }
}
@media (max-width:369px) {
    
    .content-bio{
        transform: scale(0.5);
    }
}
@media (max-width:299px) {
    
    .content-bio{
        transform: scale(0.45);
    }
}